body {
  line-height: 1;
  text-align: left;
  text-align: start;
  position: relative;
  z-index: 0;
  margin: 0;
  padding: 0;
  color: #000000;
  background: #ffffff;
  border: 0;
  word-wrap: break-word;
}

menu, ol, ul {
  list-style: none
}

blockquote, q {
  quotes: none
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none
}

ins {
  text-decoration: none
}

del {
  text-decoration: line-through
}

table {
  border-collapse: collapse;
  border-spacing: 0
}

strong, b {
