  background: transparent
}

button {
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent
}

input,
textarea,
keygen,
select,
button {
  text-rendering: auto;
  color: initial;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  margin: 0em 0em 0em 0em;
  outline: 0;
}

canvas, caption, center, cite, code, dd, del, dfn, div, dl, dt, em, embed, fieldset, font, form {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  background: transparent
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent
}

hr, html, i, iframe, img, ins, kbd, label, legend, li, menu, object, ol, p, pre, q, s, samp, small, span, strike, strong, sub {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  background: transparent
}

sup {
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent
}

table, tbody, td, tfoot, th, thead, tr, tt, u, ul, var {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  background: transparent
}

html, body {
  height: 100%;
  min-height: 100%;
}

embed, object {
  position: relative;
}


