/* page-cover */
.page-cover {
  position: fixed;
  top: 0;
  left: 0;
  right: auto;
  bottom: auto;
  width: 100%;
  height: 100%;
  min-height: 450px;
  background: #f7f7f7;
  zoom: 1;
  filter: alpha(opacity=0);
  -webkit-opacity: 0;
  -moz-opacity: 0;
  opacity: 0;
  -webkit-transition: opacity 1s linear 0s;
  -moz-transition: opacity 1s linear 0s;
  -o-transition: opacity 1s linear 0s;
  transition: opacity 1s linear 0s;
  z-index: 1999999999;
}
.page-cover_showed {
  zoom: 1;
  filter: alpha(opacity=100);
  -webkit-opacity: 1;
  -moz-opacity: 1;
  opacity: 1;
}
.page-cover_hidden {
  display: none;
}
